import _ from 'lodash';
import Sketch from '../sketch';

export default class handleFrontBack extends Sketch {
  constructor() {
    super();
    this.namespace = '|handleFrontBack';
  }

  run(name) {
    const GroupName = `@${name}`;
    const Groups = this.layer.get(this.page, GroupName);
    const Group =
      Groups[0] ||
      this.create.group({
        name: GroupName,
        parent: this.page,
      });

    const layers = _.filter(
      this.selection.layers,
      l => l.type && l.type !== 'Page' && l.type !== 'Artboard'
    );
    if (layers.length === 0) return this.ui.warn(`${name}`);
    this.setGroup(Group, layers);
    Group.locked = true;
    this.sortOrder();
    this.ui.success(`${name}`);
  }
}
