import _ from 'lodash';
import Sketch from '../sketch';
import moment from 'moment';

const GroupName = '@';
const SubGroupName = '@';

export default class handlePlate extends Sketch {
  constructor() {
    super();
    this.namespace = '|handlePlate';
    this.padding = 400;
  }

  run() {
    const isInteractiveMode = this.mode === '';
    // Symbol
    const master = this.library.getSymbol(this.library.afuxExport, `${this.mode} / `);
    if (!master) return this.ui.warn('Symbol');
    const symbolMaster = master.import();

    // 
    this.layer.deepRemove(this.page, GroupName);
    this.layer.deepRemove(this.page, SubGroupName);

    // >1
    let Layers;
    if (_.filter(this.selection.layers, l => l.type && l.type === 'Artboard').length > 1) {
      Layers = this.selection;
    } else {
      Layers = _.filter(this.page.layers, layer => layer.name[0] !== '@');
    }
    if (Layers.length === 0) return this.ui.warn('');

    // 
    let x = Infinity;
    let y = Infinity;
    let x2 = -Infinity;
    let y2 = -Infinity;

    let ShadowGroup;
    if (isInteractiveMode)
      ShadowGroup = this.create.group({
        name: SubGroupName,
        parent: this.page,
        locked: true,
      });

    Layers.forEach(l => {
      const rect = l.frame;
      if (rect.x < x) x = rect.x;
      if (rect.y < y) y = rect.y;
      if (rect.x + rect.width > x2) x2 = rect.x + rect.width;
      if (rect.y + rect.height > y2) y2 = rect.y + rect.height;
      // 
      if (isInteractiveMode && l.type === 'Artboard') {
        const Shadow = this.create.shape({
          name: l.name,
          frame: l.frame,
          style: {
            fills: [
              {
                color: '#ffffff',
                fill: 'Color',
              },
            ],
            borders: [],
            shadows: [
              {
                color: '#00000022',
                y: 40,
                blur: 100,
                spread: -20,
              },
            ],
          },
        });
        this.changeBasis(Shadow, { from: this.page, to: ShadowGroup });
        Shadow.parent = ShadowGroup;
      }
    });

    // 
    const instance = symbolMaster.createNewInstance();
    instance.frame.x = x - this.padding;
    instance.frame.y = y - this.padding * 2.1;
    instance.frame.width = x2 - x + 2 * this.padding;
    instance.frame.height = y2 - y + 3.7 * this.padding;
    instance.parent = this.page;
    instance.locked = true;

    // override-title
    const author = this.setting.get('config-name');
    if (author && author.length > 0) this.setByValue(instance, '', author);
    const title = this.page.name + ` (${this.mode})`;
    this.setByValue(instance, '', title);
    this.setByValue(instance, '', moment().format('YYYY-MM-DD'));
    const Group = this.create.group({
      name: GroupName,
      parent: this.page,
      locked: true,
    });

    this.changeBasis(instance, { from: this.page, to: Group });
    instance.parent = Group;

    // 
    const sliceLayer = this.create.slice();
    this.native.setName(sliceLayer, [title, moment().format('MMDD')].join(' '));
    this.native.setLocked(sliceLayer);
    this.native.setVisible(sliceLayer, false);
    this.native.setX(sliceLayer, instance.frame.x);
    this.native.setY(sliceLayer, instance.frame.y);
    this.native.setWidth(sliceLayer, instance.frame.width);
    this.native.setHeight(sliceLayer, instance.frame.height);
    this.native.setExport(sliceLayer, isInteractiveMode ? 0.5 : 1);
    this.native.moveToFront(sliceLayer);
    const PlateGroup = _.filter(this.native.page.layers(), l => String(l.name()) === '@')[0];
    this.native.addLayers(PlateGroup, sliceLayer);
    this.selectionClear();
    this.native.setSeleted(sliceLayer);
    this.sortOrder();
    this.ui.success('');
  }
}
