import _ from 'lodash';
import Sketch from '../sketch';

export default class handleHeight extends Sketch {
  constructor() {
    super();
    this.namespace = '|handleHeight';
  }

  run() {
    if (this.selection.isEmpty) return this.ui.warn('');
    if (this.selection.layers.length > 1) return this.ui.warn('');
    // 
    let artboard;
    if (this.selection.layers[0].type !== 'Artboard') {
      artboard = this.layer.getArtboard(this.selection.layers[0]);
      if (!artboard) return this.ui.warn('');
    } else {
      artboard = this.selection.layers[0];
    }
    // 
    let maxHeight = 0;
    _.forEach(artboard.layers, l => {
      const height = l.frame.y + l.frame.height;
      if (height > maxHeight) maxHeight = height;
    });
    artboard.frame.height = maxHeight;

    this.ui.success('');
  }
}
